<template>
  <div class="box">
    <video class="vedio_bg" loop autoplay muted plays-inline>
      <source src="../assets/bg-video.mp4" type="video/mp4"></source>
    </video>
    <span class="go" @click="go">
      进入主页
    </span>
  </div>
</template>

<script>
  export default {
    data(){
      return {
      }
    },
    methods:{
      go(){
        this.$router.push({path:'/index'})
      },
    }
  }
</script>

<style scoped>
  .box {
    background-image: url(../assets/bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
  }
  .go {
    padding: 8px 15px;
    border: 1px solid #fff;
    z-index: 3;
    position: fixed;
    bottom: 5vh;
    right: 5vh;
    cursor: pointer;
    transform: scale(.8);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    color: #fff;
    border-radius: 4px;
    background: rgba(0,0,0,.3);
  }
  .go:hover {
    animation-duration: .3s;
    animation-timing-function: ease-out;
    animation-fill-mode: both;
    animation-name: scale-up;
  }
  .vedio_bg {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100%;
    min-width: 100%;
    width: auto;
    height: auto;
    z-index: 2;
  }
  @keyframes scale-up {
      0% {
          transform: scale(.8)
      }

      100% {
          transform: scale(1)
      }
  }
</style>



